import styled from "styled-components";

export const BoxLogin = styled.div`
  width: 30%;
  margin: 30px auto;
  border: 2px solid ${(props) => props.theme.color.black.A50};
  padding: 20px;
  border-radius: 10px;
  @media screen and (max-width: ${(props) => props.theme.screen.md}) {
    width: 60%;
  }
  @media screen and (max-width: ${(props) => props.theme.screen.xs}) {
    width: 90%;
  }
`;

export const LoginInput = styled.input`
  width: 100%;
  border: 1px solid ${(props) => props.theme.color.black.A100};
  padding: 10px;
  border-radius: 6px;
  margin: 5px auto;
`;

export const LoginButton = styled.button`
  display: block;
  color: white;
  width: 100%;
  margin: 40px auto 0;
  padding: 8px 32px;
  border-radius: 5px;
  background-color: ${(props) => props.theme.color.blue.A900};
`;
