import React, { useState, useEffect } from "react";
import { BoxLogin, LoginInput, LoginButton } from "./style";
import { encryptData, decryptData } from "../../Crypted";
import { useNavigate } from "react-router-dom";

const Login = () => {
  const [formData, setFormData] = useState({ email: "", pw: "" });
  const [wrongData, setWrongData] = useState("");
  const history = useNavigate();

  const submitHandler = (e) => {
    e.preventDefault();
    if (formData.email !== "admin@gmail.com") setWrongData("Email Salah!");
    else if (formData.pw !== "adminkel1ati") setWrongData("Password Salah!");
    else {
      const salt = "6d090796-ecdf-11ea-adc1-0242ac120003";
      const encryptedData = encryptData(formData, salt);
      localStorage.setItem("mk", encryptedData);
      history("/dashboard");
    }
  };

  useEffect(() => {
    let mkLocalData = localStorage.getItem("mk");
    const salt = "6d090796-ecdf-11ea-adc1-0242ac120003";
    const originalData = decryptData(mkLocalData, salt);
    if (originalData !== null) {
      if (
        originalData.email === "admin@gmail.com" &&
        originalData.pw === "adminkel1ati"
      )
        history("/dashboard");
    }
  }, [history]);
  return (
    <BoxLogin>
      <h2 style={{ textAlign: "center" }}>Login</h2>
      <p style={{ color: "#EB131B" }}>{wrongData}</p>
      <form onSubmit={submitHandler}>
        <h6 className="bold" as="label" style={{ margin: "20px 0 -2px" }}>
          Email
        </h6>
        <LoginInput
          type="email"
          required
          name="email"
          placeholder="Masukkan email"
          onChange={(e) => {
            setFormData({ ...formData, email: e.target.value });
          }}
        />
        <h6 className="bold" as="label" style={{ margin: "20px 0 -2px" }}>
          Password
        </h6>
        <LoginInput
          type="password"
          required
          name="password"
          placeholder="Masukkan password"
          onChange={(e) => {
            setFormData({ ...formData, pw: e.target.value });
          }}
        />
        <LoginButton type="submit">Login</LoginButton>
      </form>
    </BoxLogin>
  );
};

export default Login;
