import React, { useState, useEffect } from "react";
import logo from "../../assets/logo.png";
import { Nav, NavSet, NavLogo, LogoutButton } from "./style";
import { decryptData } from "../../Crypted";
import { useNavigate } from "react-router";

const Navbar = () => {
  const [buttonLogout, setButtonLogout] = useState(false);
  const navigate = useNavigate();
  let mkLocalData = localStorage.getItem("mk");
  const salt = "6d090796-ecdf-11ea-adc1-0242ac120003";
  const originalData = decryptData(mkLocalData, salt);
  useEffect(() => {
    if (localStorage.getItem("mk") !== null) {
      if (
        originalData.email === "admin@gmail.com" &&
        originalData.pw === "adminkel1ati"
      )
        setButtonLogout(true);
    }
  }, [originalData, buttonLogout]);

  const removed = () => {
    localStorage.removeItem("mk");
    navigate("/");
  };
  return (
    <Nav>
      <NavSet>
        <NavLogo src={logo} />
        {buttonLogout === true ? (
          <LogoutButton onClick={removed}>Log Out</LogoutButton>
        ) : null}
      </NavSet>
    </Nav>
  );
};

export default Navbar;
